*{
  margin:0;
  padding:0;
}
html{
  width:100vw;
  height:100vh;
  font-size: 62.5%;
}
body,#root {
  width:100%;
  height:100%;
  font-family: 'Days One',sans-serif;
  padding:2rem;
  box-sizing: border-box;
}
#root { /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #b2b2b2,#909090); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
p{
  font-family: 'Open Sans Condensed', sans-serif;
}
.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding:2rem 2rem 0rem;
  border:1px dotted #fbfcfc; 
}
.dashboard {
  width:30rem;
  padding:2rem;
  text-align: center;
}
.dashboard h2 {
  text-transform: uppercase;
  padding-bottom:1rem;
  font-size: 1.5rem;
  
}
.dashboard p {
  font-size: 2rem;
}
.game-board h1 {
  text-transform: uppercase;
  padding-bottom:1.5rem;
  text-align: center;
  font-size: 2rem;
}
.board {
  width:45rem;
  height:45rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 0px 4px 2px rgba(0,0,0,0.3);
  padding:2rem; 
  
}
.result-board {
  width:100%;
  height: 100%;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #333333;
}
.result-board .game-over,.result-board .the-winner {
  font-size: 4rem;
  font-weight: 700;
  font-family: 'Days One',sans-serif;
  color:#fbfcfc;
  margin-bottom:1rem;
}
.result-board .game-pad{
  font-size: 3.2rem;
  margin-bottom:2rem;
}
.result-board .the-winner {
  font-family: 'Open Sans Condensed', sans-serif;
  font-weight: 300;
}

.square {
  width:15rem;
  height:15rem;
  outline:1px solid #333333;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  background-color: #fcc01c;
  cursor:pointer;
  color:#fbfcfc;
  text-align: center;
  font-size: 6rem;
  line-height: 15rem;
}
.square:hover{
  background-color: red;
}
.active-player-wrapper {
  box-shadow: 0px 0px 4px 2px rgba(0,0,0,0.3);
  padding: 1rem;
  width:-webkit-max-content;
  width:-moz-max-content;
  width:max-content;
  margin:1rem auto;
}
.square.active-player {
  cursor:auto;
  cursor:initial;
  background-color: red
}
.square.active-player:hover{
  background-color: red
}
.reset-game,.try-again {
  background-color:#9dd6f9;
  border:1px solid #5eb6ef;
  outline:none;
  cursor:pointer;
  padding:1rem 1.5rem;
  color:#333333;
  font-weight: 700;
  font-size: 1.5rem;
}
.try-again {
  width:-webkit-max-content;
  width:-moz-max-content;
  width:max-content;
}
.reset-game:hover,.try-again:hover{
  background-color:#5eb6ef;
  border:1px solid #9dd6f9;
  color: #333333;
}
.tips {
  min-height: 5rem;
  padding:1rem;
  text-align: center;
  font-size: 2rem;
  line-height: 5rem;
}
